body {
	background: -webkit-gradient(linear, left top, left bottom, from(#e0fafc), to(#b5f7fc));
	background: -webkit-linear-gradient(#e0fafc, #b5f7fc);
	background: -o-linear-gradient(#e0fafc, #b5f7fc);
	background: linear-gradient(#e0fafc, #b5f7fc);
	color: #015156;
	min-height: 100vh;
}

.App {
  text-align: center;
  
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

li {
	margin: 40px auto;
	background: #7cf1f9;
	padding: 20px;
	width: 500px;
	list-style: none;
	border-radius: 30px;
	position: relative;
}

li::before {
	content:"";
	position: absolute;
	bottom: -14px;
	right: 20px;
	width: 55px;
	height: 55px;
	background-color: #7cf1f9;
	-webkit-transform: rotate(23deg);
	    -ms-transform: rotate(23deg);
	        transform: rotate(23deg);
    
}

li button {
	margin: 20px 5px 0;
	min-width: 25px;
	cursor: pointer;
	border: 1px solid #015156;
	background: #b5f7fc;
}

li button:hover {
	background: #e0fafc
}

.edit {
	margin-left: 100px;
}

.new {
	height: 40px;
	background: -webkit-gradient(linear, left top, left bottom, from(#b5f7fc), to(#7cf1f9));
	background: -webkit-linear-gradient(#b5f7fc, #7cf1f9);
	background: -o-linear-gradient(#b5f7fc, #7cf1f9);
	background: linear-gradient(#b5f7fc, #7cf1f9);
	font-size: 16px;
	font-weight: 700;
	padding: 10px;
	cursor: pointer;
	color: #015156;
	border-radius: 10px;
}

.new:hover {
	background: -webkit-gradient(linear, left top, left bottom, from(#7cf1f9), to(#b5f7fc));
	background: -webkit-linear-gradient(#7cf1f9, #b5f7fc);
	background: -o-linear-gradient(#7cf1f9, #b5f7fc);
	background: linear-gradient(#7cf1f9, #b5f7fc);
}

textarea {
    resize: none;
	display: block;
	margin: 20px auto;
	background: #b5f7fc;
}



